.App {
  text-align: center;
  font-family: Arial, sans-serif;
  position: relative;
}

.content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

h1 {
  font-size: 3rem;
}

p {
  font-size: 1.2rem;
}

button {
  margin: 10px;
  padding: 10px 20px;
  background-color: #0056b3;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #004494;
}
